body,html,#root{
  height:100%;
  width:100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
}
#root{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.score{
  float:right;
}
.panel{
  overflow: hidden;
}
.panelCell{
  background-color:#ddd;
  border:1px solid #ccc;
}
.L{
  background:red;
  border:1px solid #ddd;
}
.J{
  background:pink;
  border:1px solid #ddd;
}
.I{
  background:green;
  border:1px solid #ddd;
}
.O{
  background:yellow;
  border:1px solid #ddd;
}
.T{
  background:orange;
  border:1px solid #ddd;
}
.S{
  background:rgb(129, 188, 236);
  border:1px solid #ddd;
}
.Z{
  background:cyan;
  border:1px solid #ddd;
}


